
footer {
    display: flex;
    flex-wrap: wrap;
}

footer>section:first-child {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

footer>section:first-child>svg {
    color: blue;
    font-size: 4em;
    margin: 0.5em;
    animation: spin infinite 5s linear;
}

footer>section:nth-child(2) {
    flex: 4 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

footer>section:nth-child(2)>p {
    font-weight: bold;
}
.div-task-form>form>.form-header {
  display: flex;
  font-style: italic;
  justify-content: center;
  margin-bottom: 1em;
}

.div-task-form>form>.form-control-container {
  display: flex;
  align-items: center;
}

.div-task-form>form>.form-control-container .form-control:focus,
.div-task-form>form .form-check .form-check-input:focus {
  border-color: #9599a0 !important;
  box-shadow: 0 0 0 0.25rem #b1b4b75f !important;
}

.div-task-form>form>.form-control-container>.react-icon {
  margin-right: .5em;
}

.div-task-form>form .form-check>.form-check-input:checked {
  background-color: #686b70;
  border-color: #88898a;
}

.div-task-form>form>.form-submit {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tasks-list-section>section:first-child {
  text-align: center;
}
.task-component-div {
    margin: 0.25em;
    padding: 0.25em;
    border-radius: 0.5em;
    background-color: #fefce7;
}

.task-component-div>.card-body {
    display: flex;
    flex-direction: column;
}

.task-component-div .task-component-btns {
    align-self: center;
}

.task-component-div .task-component-btns button:first-child {
    font-size: 1.4em;
}

.task-component-div .task-component-btns button:hover {
    opacity: 80%;
    box-shadow: 1px 1px 3px goldenrod;
}
.task-card {
    border: none;
    position: relative;
    background-color: #f9f1e8;
    box-shadow: 1px 1px 3px #ffffff8f;
}

.task-card>.react-icon {
    position: absolute;
    color: orangered;
    top: .25em;
    right: .45em;
    font-size: 1.5em;
}

.task-card>.react-icon:hover {
    cursor: pointer;
    color: #f1845c;
}

.task-card>.react-icon:hover::after {
    content: "X";
    position: absolute;
    top: .1em;
    right: .2em;
    color: black;
}

body{
    background-image: linear-gradient(#a8d6e5, #a8c0c7);
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
}

.todo-container {
    display: flex;
    flex-direction: column;    
}

.todo-container>section {
    display: flex;
    margin: 0.25em;
    border-radius: 0.5em;
    border: none;
}

.todo-container>section>section:nth-child(1) {
    flex: 1 1;
}

.todo-container>section>section:nth-child(2) {
    flex: 2 1;
}

.gray-container {
    margin: 0.5em;
    padding: 0.5em;
    border-radius: 0.5em;
    margin-bottom: .9em;
    background-color: #ffffffaa;
    box-shadow: 1px 1px 7px #979696a2;
}

.gray-container:hover {
    transform: translateX(.05em) translateY(.05em);
}

@media screen and (max-width: 1200px) {
    .todo-container>section {
        display: flex;
        flex-direction: column;
    }

    .todo-container>section>section {
        margin: .15em;
    }

}

@keyframes spin {
    0% {
        transform: rotate(0deg);
        color: red;
    }

    35% {
        color: green;
    }

    70% {
        color: blue;
    }

    100% {
        transform: rotate(360deg);
        color: red;
    }
}
nav > section {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffffaa;
}

nav svg {
    font-size: 2em;
    margin: 0.5em;
    animation: spin infinite 5s linear;
}

